/* Get In Touch Page Styles */
.getintouch-page {
  background: #f4f6f8;
  color: var(--navy-heading);
  font-family: 'Inter', 'Segoe UI', 'Roboto', Arial, sans-serif;
}

/* Hero Section */
.getintouch-hero {
  background: linear-gradient(160deg, #f4f6f8 0%, #e3f0fa 100%);
  padding: 6rem 0 5rem 0;
  text-align: center;
  border-bottom: 1px solid var(--border-light);
}
.getintouch-hero-title {
  font-size: 3rem;
  font-weight: 650;
  color: var(--navy-heading);
  margin-bottom: 1.2rem;
  letter-spacing: 1px;
}
.getintouch-hero-subtitle {
  font-size: 1.3rem;
  color: var(--text-secondary);
  font-weight: 500;
  max-width: 700px;
  margin: 0 auto;
  line-height: 1.5;
}

/* Large screen responsive font sizes */
@media (min-width: 1200px) {
  .getintouch-hero-title {
    font-size: 3.5rem;
  }
  .getintouch-hero-subtitle {
    font-size: 1.5rem;
  }
  .getintouch-success {
    font-size: 1.4rem;
  }
  .getintouch-form label {
    font-size: 1.2rem;
  }
  .getintouch-form input,
  .getintouch-form textarea {
    font-size: 1.15rem;
  }
  .getintouch-submit {
    font-size: 1.25rem;
  }
}

/* Form Section */
.getintouch-form-section {
  padding: 4rem 0 4rem 0;
  display: flex;
  justify-content: center;
}
.getintouch-form-container {
  background: #fff;
  border-radius: 1.2rem;
  box-shadow: 0 4px 24px #57bceb22;
  border: 1.5px solid var(--border-light);
  padding: 2.5rem 2.5rem 2rem 2.5rem;
  max-width: 420px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.getintouch-success {
  color: var(--science-blue);
  font-size: 1.2rem;
  font-weight: 600;
  text-align: center;
  padding: 2rem 0;
}
.getintouch-form {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
.getintouch-form label {
  font-size: 1.05rem;
  color: var(--navy-heading);
  font-weight: 600;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.getintouch-form input,
.getintouch-form textarea {
  border: 1.5px solid var(--border-light);
  border-radius: 0.7rem;
  padding: 0.7rem 1rem;
  font-size: 1rem;
  font-family: inherit;
  margin-top: 0.2rem;
  background: #f9fafd;
  color: var(--navy-heading);
  transition: border 0.2s;
}
.getintouch-form input:focus,
.getintouch-form textarea:focus {
  border: 1.5px solid var(--science-blue);
  outline: none;
}
.getintouch-submit {
  background: linear-gradient(90deg, #57bceb 0%, #289ad7 100%);
  color: #fff;
  font-weight: 700;
  font-size: 1.1rem;
  border: none;
  border-radius: 0.7rem;
  padding: 0.8rem 2.2rem;
  box-shadow: 0 2px 8px #57bceb33;
  cursor: pointer;
  margin-top: 0.5rem;
  transition: background 0.2s, color 0.2s, box-shadow 0.2s;
}
.getintouch-submit:hover {
  background: linear-gradient(90deg, #289ad7 0%, #57bceb 100%);
  color: #fff;
}

@media (max-width: 600px) {
  .getintouch-form-container {
    padding: 1.2rem 0.7rem 1rem 0.7rem;
    max-width: 98vw;
  }
} 

@media (max-width: 900px){
  .getintouch-hero {
    padding: 6rem 2rem 5rem 2rem;
  }

  .getintouch-hero-title {
    font-size: 2rem;
  }

  .getintouch-hero-subtitle {
    font-size: 1rem;
  }
  .getintouch-form-section {
    padding: 4rem 0 4rem 0;
    display: flex;
    justify-content: center;
  }
  .getintouch-form-container {
    background: #fff;
    border-radius: 1.2rem;
    box-shadow: 0 4px 24px #57bceb22;
    border: 1.5px solid var(--border-light);
    padding: 2.5rem 2.5rem 2rem 2.5rem;
    max-width: 420px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

/* Scroll-triggered animations */
.animate-on-scroll {
  opacity: 0;
  transition: all 0.6s cubic-bezier(0.4, 0, 0.2, 1);
  will-change: transform, opacity;
  backface-visibility: hidden;
}

.animate-on-scroll.visible {
  opacity: 1;
  will-change: auto;
}

/* Specific animation classes */
.slide-up {
  transform: translateY(30px);
}

.slide-up.visible {
  transform: translateY(0);
}

/* Mobile-specific animation adjustments */
@media (max-width: 767px) {
  .animate-on-scroll {
    transition-duration: 0.4s;
  }
  
  .slide-up {
    transform: translateY(20px);
  }
}

/* Reduce motion for users who prefer it */
@media (prefers-reduced-motion: reduce) {
  .animate-on-scroll {
    transition: none !important;
    animation: none !important;
    transform: none !important;
    opacity: 1 !important;
  }
}