/* Team Page Styles */
.team-page {
  background: #f4f6f8;
  color: var(--navy-heading);
  font-family: 'Inter', 'Segoe UI', 'Roboto', Arial, sans-serif;
}

/* Hero Section */
.team-hero {
  background: linear-gradient(160deg, #f4f6f8 0%, #e3f0fa 100%);
  padding: 6rem 0 5rem 0;
  text-align: center;
  border-bottom: 1px solid var(--border-light);
}

.team-hero-title {
  font-size: 3rem;
  font-weight: 650;
  color: var(--navy-heading);
  margin-bottom: 1.2rem;
  letter-spacing: 1px;
}

.team-hero-subtitle {
  font-size: 1.3rem;
  color: var(--text-secondary);
  font-weight: 500;
  max-width: 700px;
  margin: 0 auto;
  line-height: 1.5;
}

/* Large screen responsive font sizes */
@media (min-width: 1200px) {
  .team-hero-title {
    font-size: 3.5rem;
  }

  .team-hero-subtitle {
    font-size: 1.5rem;
  }

  .team-section-title {
    font-size: 2.5rem;
  }

  .team-ceo-name {
    font-size: 1.8rem;
  }

  .team-ceo-title {
    font-size: 1.3rem;
  }

  .team-ceo-bio {
    font-size: 1.25rem;
  }

  .team-member-name {
    font-size: 1.3rem;
  }

  .team-member-title {
    font-size: 1.2rem;
  }

  .team-member-bio {
    font-size: 1.15rem;
  }
}

/* CEO Section */
.team-ceo-section {
  background: #fff;
  padding: 4rem 0 4rem 0;
  border-bottom: 1px solid var(--border-light);
  display: flex;
  justify-content: center;
}

.team-ceo-card {
  display: flex;
  align-items: center;
  gap: 2.5rem;
  background: #f9fafd;
  border-radius: 1.5rem;
  box-shadow: 0 4px 24px #57bceb22;
  border: 1.5px solid var(--border-light);
  padding: 2.5rem 3rem;
  max-width: 700px;
  width: 100%;
}

.team-ceo-photo {
  width: 120px;
  height: 120px;
  object-fit: cover;
  border-radius: 1.2rem;
  background: #fff;
  box-shadow: 0 2px 8px #e5e7eb33;
}

.team-ceo-info {
  flex: 1;
}

.team-ceo-name {
  font-size: 1.5rem;
  font-weight: 800;
  color: var(--science-blue);
  margin-bottom: 0.3rem;
}

.team-ceo-title {
  font-size: 1.1rem;
  color: var(--navy-heading);
  font-weight: 600;
  margin-bottom: 0.7rem;
}

.team-ceo-bio {
  color: var(--text-secondary);
  font-size: 1.08rem;
  line-height: 1.6;
}

/* Members Section */
.team-members-section {
  background: #f9fafd;
  padding: 4rem 0 4rem 0;
}

.team-section-title {
  font-size: 2rem;
  font-weight: 800;
  color: var(--science-blue);
  margin-bottom: 2.5rem;
  letter-spacing: 0.5px;
  text-align: center;
}

.team-members-grid {
  display: flex;
  gap: 2rem;
  justify-content: center;
  flex-wrap: wrap;
}

.team-member-card {
  background: #fff;
  border-radius: 1.2rem;
  box-shadow: 0 4px 24px #57bceb22;
  border: 1.5px solid var(--border-light);
  padding: 2.2rem 1.5rem 1.5rem 1.5rem;
  width: 300px;
  min-width: 160px;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: box-shadow 0.2s, transform 0.2s, background 0.2s, border 0.2s;
}

.team-member-card:hover {
  box-shadow: 0 0 15px 0.1px var(--curious-blue-transparent);
  border: 2px solid var(--curious-blue-transparent);
  transform: translateY(-4px) scale(1.03);
}

.team-member-photo {
  width: 80px;
  height: 80px;
  object-fit: cover;
  border-radius: 1rem;
  background: #fff;
  box-shadow: 0 2px 8px #e5e7eb33;
  margin-bottom: 1.1rem;
}

.team-member-name {
  font-size: 1.1rem;
  font-weight: 700;
  color: var(--navy-heading);
  margin-bottom: 0.3rem;
  text-align: center;
}

.team-member-title {
  font-size: 1rem;
  color: var(--science-blue);
  font-weight: 600;
  margin-bottom: 0.7rem;
  text-align: center;
}

.team-member-bio {
  color: var(--text-secondary);
  font-size: 0.98rem;
  text-align: center;
  line-height: 1.5;
}

@media (max-width: 900px) {
  .team-ceo-card {
    flex-direction: column;
    gap: 1.2rem;
    padding: 2rem 1.2rem;
    max-width: 70vw;
  }

  .team-members-grid {
    flex-direction: column;
    gap: 1.2rem;
    align-items: center;
  }

  .team-member-card {
    width: 90vw;
    max-width: 350px;
    margin: 0 auto;
  }
  .team-hero{
    padding: 6rem 2rem 5rem 2rem;
  }
  .team-hero-title {
    font-size: 2rem;
  }
  .team-hero-subtitle {
    font-size: 1rem;
  }
}

.team-email {
  margin-top: 0.5rem;
}

.team-email-link {
  color: var(--science-blue);
  font-size: 0.95rem;
  font-weight: 500;
  text-decoration: underline;
  word-break: break-all;
}

.team-email-link:hover {
  color: var(--curious-blue);
}

/* Scroll-triggered animations */
.animate-on-scroll {
  opacity: 0;
  transition: all 0.6s cubic-bezier(0.4, 0, 0.2, 1);
  will-change: transform, opacity;
  backface-visibility: hidden;
}

.animate-on-scroll.visible {
  opacity: 1;
  will-change: auto;
}

/* Specific animation classes */
.slide-up {
  transform: translateY(30px);
}

.slide-up.visible {
  transform: translateY(0);
}

/* Mobile-specific animation adjustments */
@media (max-width: 767px) {
  .animate-on-scroll {
    transition-duration: 0.4s;
  }
  
  .slide-up {
    transform: translateY(20px);
  }
}

/* Reduce motion for users who prefer it */
@media (prefers-reduced-motion: reduce) {
  .animate-on-scroll {
    transition: none !important;
    animation: none !important;
    transform: none !important;
    opacity: 1 !important;
  }
}